import * as shapefile from 'shapefile'; // https://www.npmjs.com/package/shapefile

export class Solution {
    public divHost: HTMLElement;

    divLog: HTMLDivElement; 
    constructor(id: string) {
        this.divHost = document.getElementById(id)

        // Center align
        this.divHost.style.display = 'flex';
        this.divHost.style.alignItems = 'center';

        const div = document.createElement('div');
        div.style.marginLeft = 'auto';
        div.style.marginRight = 'auto';
        // div.style.backgroundColor = '#555';
        div.style.width = '60%';
        div.style.height = '60%';

        this.divLog = div;
        this.divHost.appendChild(div);

        // this.renderer = new Renderer(div);

        const path = 'https://raw.githubusercontent.com/NamjuLee/data/master/shape/cb_2018_us_county_5m/cb_2018_us_county_5m.shp';

        this.importAsJSON(path)
        // this.processShapefileData(path);
    }
    public async processShapefileData(path: string) {
        const shapefileData = await this.importingShape(path);
        if (shapefileData) {
            console.log({shapefileData});
        } else {
            console.debug("empty shapefile");
        }
    }
    public async importingShape(path: string) {
        try {
            const source = await shapefile.open(path);
            const allData = [];
            let result;
            while ((result = await source.read()) && !result.done) {
                allData.push(result.value);
            }
            return allData;
        } catch (error) {
            console.error(error);
            return null;
        }
    }

    public async importAsJSON(path: string) {
        const geojson = await shapefile.read(path)
        // console.log(JSON.stringify(geojson))
        const data = JSON.stringify(geojson);

        downloadBlob(data, 'shape.json')

        // this.divHost.innerText = 
    }
    public destroy() {
        try {
            while (this.divHost && this.divHost.lastElementChild) {
                const lastChild = this.divHost.lastElementChild as Node;
                this.divHost.removeChild(lastChild);
            }
        } catch (error) {
            console.error('Error removing child elements:', error);
        }
        
    }
}


const downloadBlob = (content, filename, contentType = 'text/json;charset=utf-8;') => {
    // Create a blob
    var blob = new Blob([content], { type: contentType });
    var url = URL.createObjectURL(blob);

    // Create a link to download it
    var pom = document.createElement('a');
    pom.href = url;
    pom.setAttribute('download', filename);
    pom.click();
}